
.blockCard {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    position: relative;
  
  }
  
  .blockCard span {
    position: absolute;
    background-color: blueviolet;
    width: 70px;
    height: 35px;
    border-radius: 20px;
    color: #fff;
    left: 20px;
    display: flex;
    justify-content: center;
    font-size: 20px;
    z-index: 99;
  }
  
  .blockCard button{
    background-color: blueviolet;
    color: #fff;
    font-weight: 700;
    z-index: 999;
    bottom: 200px;
    margin: 0 auto;
    margin-top: 40px;
 }
  
  .blockCard button:hover {
    color: blueviolet;
    background-color: transparent;
    cursor: pointer;
  }
  
  .blockCard img:hover {
    transform: scale(1.05);
  }
  
  .blockCard img {
    border-radius: 10px; 
    margin-left: 20px;   
    transition: transform 0.3s ease;
    height: 300px;
    width: 280px;
    cursor: pointer;
  }
  
  .blockCard h3 {
    margin-top: 20px;
    font-size: 20px;
    color: #ffffff;
    cursor: pointer;
    display: inline-block;
  }
  
  .blockCard p {
    cursor: pointer;
    font-size: 15px;
    color: #ffffff;
    margin: 0;
  }
  
  .oldPrice {
  
    text-decoration: line-through;
  }

